<template>
  <div class="music-card">
    <a-row class="song-info" type="flex" align="top">
      <a-col :span="20">
        <div>
          <p class="song-title">{{song.title}}</p>
          <p class="song-artist">{{song.artist}}</p>
        </div>
      </a-col>
      <a-col :span="4">
        <div class="song-genre">{{song.genre}}</div>
      </a-col>
    </a-row>
    <audio :src="musicPath" class="audio-player" controls="controls" preload="none">
      <source :src="musicPath" />
    </audio>
  </div>
</template>

<script>
export default {
  data() {
    return {
      musicPath: `./music/${this.song.title}.mp3`
    }
  },
  props: ["song"],
  watch: {
    song: function(val){
      this.musicPath =  `./music/${val.title}.mp3`;
    }
  }
};
</script>

