<template>
  <div class="music">
    <a-row type="flex">
      <a-col :span="18">
        <SubNavBar :items="subNavBarItems" @navChange="navChangeHandler" class="sub-navbar" />
        <div class="music-card-container">
          <MusicCard v-for=" (song,index) in music" :key="index" :song="song" />
        </div>
      </a-col>
      <a-col :span="6">
        <ContactCard class="contact-card"/>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import SubNavBar from "../components/SubNavBar";
import ContactCard from "../components/ContactCard";
import MusicCard from "../components/MusicCard";
import songs from "../assets/music.json";
export default {
  data() {
    return {
      name: "SubNavBar",
      subNavBarItems: [
        { key: "all", text: "全部" },
        { key: "romantic", text: "浪漫" },
        { key: "epic", text: "史诗" },
        { key: "drama", text: "戏剧" },
        { key: "percussion", text: "打击乐" },
        { key: "other", text: "其他" }
      ],
      genreKeys: {
        all: "全部",
        romantic: "浪漫",
        epic: "史诗",
        drama: "戏剧",
        percussion: "打击乐",
        other: ["恐怖", "世界", "杂项", "电子", "乐观"]
      },
      music: songs
    };
  },
  components: {
    SubNavBar,
    ContactCard,
    MusicCard
  },
  methods: {
    navChangeHandler(value) {
      let newMusic;
      if(value === "all") {
        newMusic = songs;
      }
      else if (value === "other") {
        newMusic = songs.filter(song => {
          return this.genreKeys.other.includes(song.genre);
        });
      } else {
        const genreKey = this.genreKeys[value];
        newMusic = songs.filter(song => {
          return song.genre === genreKey;
        });
      }
      this.music = newMusic;
    }
  }
};
</script>

