<template>
  <div class="progress-bar" :style="{'width':this.progressWidth}"></div>
</template>

<script>
export default {
  name: 'ProgressBar',
  data() {
    return {
      progressWidth: 0
    };
  },
  mounted:function() {
    window.addEventListener('scroll',this.scrollHandler);
  },
  methods: {
    scrollHandler() {
      var scrollTop = window.pageYOffset;
      this.progressWidth = scrollTop / (document.body.scrollHeight -document.body.clientHeight) * 100 + '%';
    }
  },
  watch: {
    $route:{
        handler(){
          this.progressWidth = ''
        }
      }
  }
};
</script>

<style scoped>
.progress-bar {
  height:.4%;
  background-color:#1790FC;
  position:fixed;
  z-index:999;
  top:0;
}
</style>