import Vue from 'vue'
import Router from 'vue-router'
import Home from './view/Home'
import Display from './view/Display'
import Music from './view/Music'
import Animation from './view/Animation'

Vue.use(Router)

export default new Router({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path:'/',
      name:'home',
      component: Home
    },
    {
      path:'/display',
      name:'display',
      component: Display
    },
    {
      path:'/music',
      name:'music',
      component: Music
    },
    {
      path:'/animation',
      name:'animation',
      component: Animation
    }
  ]
})